.home-work-item {
  width: 250px;
  height: 150px;
  background-color: rgb(235, 233, 233);
  border-radius: 10px;
  padding: 5px;
  margin: 10px;

  @media (max-width: 400px)  {
    width: 250px;
    height: 150px;
    background-color: rgb(235, 233, 233);
    border-radius: 10px;
    padding: 5px;
    margin: 10px;
    margin-left: auto;
    margin-right: auto;
    margin: 5px;
  }
}

.active-homework {
  border: 3px solid green;
}